.form-control {
  height: auto;
}

.custom-select {
  background: none;
  @include getIcon("chevronDown", $gray-primary, 25);
  background-repeat: no-repeat;
  background-position: calc(100% - 0.7rem) 50%;
  width: 100%;
  height: auto;

  &.select-small {
    @extend .display-29;
    padding: 0.1875rem 0.9375rem;
  }

  &::-ms-expand {
    display: none;
  }
}

.custom-search {
  height: auto;

  .search-button {
    &:hover {
      background-color: transparent !important;
    }

    height: remCalc(40);

    &::before {
      @include getIcon("search", $gray-primary, 25);
      content: '';
      position: relative;
      display: inline-block;
    }

    &:focus {
      outline: none;
      background-color: transparent !important;
    }
  }

  .search-button-absolute {
    top: 0;
    right: 0;
  }

  .search-input {
    @include placeholderColor($gray-primary);
  }
}

.invalid-feedback {
  position: relative;

  &::before {
    @include getIcon("close", $white, 16);
    background-color: $red;
    background-position: 50% 50%;
    border-radius: 50%;
    content: "";
    left: 0;
    top: remCalc(4);
    position: absolute;
  }
}

.is-invalid {
  .form-control {
    border-color: $red;
  }

  .invalid-feedback {
    display: block;
  }
}

.custom-checkbox {
  .custom-control-input {
    ~ .custom-control-label {
      &::before {
        top: remCalc(4);
      }
    }

    &:checked ~ .custom-control-label {
      &::after {
        @include getIcon("checkmarkSmall", $gray-primary, 45);
        background-position: 50% 50%;
        background-size: contain;
        width: remCalc(16);
        height: remCalc(16);
        top: remCalc(4);
      }
    }

    &:disabled ~ .custom-control-label {
      color: $gray-dark;

      &::before {
        background-color: $white;
        border-color: $gray-dark;
        top: remCalc(4);
      }
    }
  }
}

.custom-checkbox.custom-checkbox--round,
.custom-radio.custom-radio--round {
  .custom-control-input ~ .custom-control-label {
    &::before {
      border-color: $gray-secondary;
      border-radius: 50%;
      width: remCalc(24);
      height: remCalc(24);
    }

    &::after {
      width: remCalc(24);
      height: remCalc(24);
    }

    &--inline {
      &::before,
      &::after {
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  .custom-control-input:focus ~ .custom-control-label {
    &::before {
      border-color: $gray-line;
    }
  }

  .custom-control-input:checked ~ .custom-control-label {
    &::before {
      background-color: $primary;
      border-color: $primary;
      border-radius: 50%;
    }

    &::after {
      @include getIcon("checkmarkSmall", $white, 100);
      background-position: 50% 50%;
      background-size: contain;
      width: remCalc(24);
      height: remCalc(24);
    }
  }


  .custom-control-input:disabled ~ .custom-control-label {
    &::before {
      background-color: $white;
      border-color: $gray-line;
    }
  }

  .custom-control-input:not(:disabled) ~ .custom-control-label {
    &:hover {
      cursor: pointer;
    }
  }
}

.has-warning:not(.is-invalid) .form-control {
  border-color: $warning;
  background-color: theme-color-level($warning, $alert-bg-level);
}
.has-warning-border:not(.is-invalid) .form-control {
  border-color: $warning;
}

.payment-radio-buttons{
    &::before {
      top: 1.5rem;
    }

    &::after {
      top: 1.5rem;
    }
  }

