$sideMenuWidth: 88vw;
$animationTime: .5s;

.mobile-side-menu {

  .sideMenuOverlay {
    visibility: hidden;
    // Border bleibt sichtbar
    width: calc(#{$sideMenuWidth} - 0.25rem);
    position: fixed;
    top: 0;
    bottom: 0;
    left: -$sideMenuWidth;
    background-color: $white;
    transition: $animationTime;

    &.active {
      @include z-index(1);
      visibility: visible;
      left: 0;
    }

    .scrollable {
      overflow-y: auto;
    }

    .sideMenuOverlayHeadline {
      bottom: -1px;
    }
  }

  .btn-collapsible {
    &.collapse-sidemenu::before {
      background-image: getIconUrl("chevronUp", $dark, 24);
    }

    &.collapsed {
      &.collapse-sidemenu::before {
        background-image: getIconUrl("chevronDown", $dark, 24);
      }
    }
  }
}