.filterchip {

  height: remCalc(24);

  .filterchipClearAllFilters {
    top: remCalc(1);

    &::before {
      background-image: getIconUrl("close", $dark, 21);
      content: '';
      display: inline-block;
      width: remCalc(20);
      height: remCalc(20);
      vertical-align: bottom;
    }
  }

  .filterchipcontent {
    top: remCalc(1);

    &::after {
      background-image: getIconUrl("close", $dark, 21);
      content: '';
      display: inline-block;
      width: remCalc(20);
      height: remCalc(20);
      vertical-align: bottom;

    }
  }
}