// map for breakpoint specific line-height & font-size
$display-map: (
        display-1: (
                font-size: 32,
                font-size-md: 64,
                line-height: 40,
                line-height-md: 88
        ),
        display-2: (
                font-size: 32,
                font-size-md: 56,
                line-height: 40,
                line-height-md: 72
        ),
        display-3: (
                font-size: 32,
                font-size-md: 48,
                line-height: 40,
                line-height-md: 64
        ),
        display-4:  (
                font-size: 32,
                line-height: 40
        ),
        display-5:  (
                font-size: 24,
                line-height: 32
        ),
        display-6:  (
                font-size: 20,
                line-height: 28
        ),
        display-7:  (
                font-size: 16,
                line-height: 24
        ),
        display-8:  (
                font-size: 24,
                font-size-md: 32,
                line-height: 32,
                line-height-md: 40
        ),
        display-9:  (
                font-size: 16,
                line-height: 24
        ),
        display-10:  (
                font-size: 20,
                font-size-md: 32,
                line-height: 28,
                line-height-md: 40
        ),
        display-11:  (
                font-size: 18,
                line-height: 26
        ),
        display-21:  (
                font-size: 16,
                line-height: 26
        ),
        display-22: (
                font-size: 14,
                line-height: 24
        ),
        display-23:  (
                font-size: 14,
                line-height: 24
        ),
        display-24:  (
                font-size: 12,
                line-height: 20
        ),
        display-25:  (
                font-size: 12,
                line-height: 20
        ),
        display-26:  (
                font-size: 10,
                line-height: 14
        ),
        display-27:  (
                font-size: 12,
                line-height: 16
        ),
        display-28:  (
                font-size: 14,
                line-height: 40,
                line-height-lg: 32
        ),
        display-29:  (
                font-size: 12,
                line-height: 24
        ),
        display-30:  (
                font-size: 24,
                line-height: 32
        ),
        display-31:  (
                font-size: 14,
                line-height: 40
        ),
        display-32:  (
                font-size: 12,
                font-size-md: 14,
                line-height: 20,
                line-height-md: 24
        )
);

@function map-deep-get($display-map, $keys...) {
  @each $key in $keys {
    $display-map: map-get($display-map, $key);
  }
  @return $display-map;
}

////////////////////////////
/// Type display classes ///
////////////////////////////

////////////////////////// Headline classes  //////////////////////////////

[class*="display-"] {
  font-family: $font-family-base;
}

h1, .h1 { @extend .display-1; }
h2, .h2 { @extend .display-2; }
h3, .h3 { @extend .display-3; }
h4, .h4 { @extend .display-4; }
h5, .h5 { @extend .display-8; }
h6, .h6 { @extend .display-5; }

/* Open Sans SemiBold, 32/40px | 64/88px */
.display-1 {
  font-size: remCalc(map-deep-get($display-map, 'display-1', 'font-size'));
  line-height: remCalc(map-deep-get($display-map, 'display-1', 'line-height'));
  font-weight: 600;
  @include media-breakpoint-up(md) {
    font-size: remCalc(map-deep-get($display-map, 'display-1', 'font-size-md'));
    line-height: remCalc(map-deep-get($display-map, 'display-1', 'line-height-md'));
  }
}

