.image-viewer {
  // Siehe Definition in ModalProductImageViewer.ts
  min-width: remCalc(200);
  min-height: remCalc(300);
}

.image-viewer-carousel{
  .slick-disabled {
    opacity: 0;
  }

  .slick-slide {
    // Generiertes Div von Slick, deshalb keine Bootstrap-Klasse
    padding: 0 remCalc(4);
  }
}

// Überschreiben von viewerjs-Styles
// Bootstrap-Klassen leider nicht möglich
.viewer-toolbar {
  > ul > li:not(:last-child) {
    margin-right: remCalc(24);
  }

  .viewer-zoom-in:before {
    @include getIcon("plus", $white, 40);
  }

  .viewer-zoom-out:before {
    @include getIcon("minus", $white, 40);
  }

  .viewer-zoom-in, .viewer-zoom-out {
    width: remCalc(40);
    height: remCalc(40);
    background-color: $dark;
    opacity: 0.5;

    &:hover {
      background-color: $dark;
      opacity: 0.8;
    }

    &:before {
      margin: 0;
      background-size: auto;
      background-position: 0;
    }
  }
}



