@mixin getSpinner($thickness, $diameter) {
  animation: 2s linear infinite spinner;
  border: solid $thickness $light;
  border-bottom-color: $spinner-border-bottom-color;
  border-radius: 50%;
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: $diameter;
  height: $diameter;
  transform: translate3d(-50%, -50%, 0);
  transform-origin: center;
  will-change: transform;
}

@keyframes spinner {
  0% {
    transform: translate3d(-50%, -50%, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(-50%, -50%, 0) rotate(360deg);
  }
}

.spinner {
  opacity: 0.4;
  &::before {
    @include getSpinner(remCalc(4), remCalc(100));
    opacity: 3;
  }
}

.spinner-small {
  position: relative;

  &::after {
    @include getSpinner(remCalc(2), remCalc(40));
  }
}