
ff-record-list {

  @include media-breakpoint-only(xs) {
    ff-record:nth-child(even) {
      padding-left: remCalc(8);
    }

    ff-record:nth-child(odd) {
      padding-right: remCalc(8);
    }
  }

  @include media-breakpoint-only(sm) {
    ff-record:nth-child(even) {
      padding-left: remCalc(8);
    }

    ff-record:nth-child(odd) {
      padding-right: remCalc(8);
    }
  }

  ff-record {

    .thumb {
      background-size: cover;

      //150% von der width, um da wo das Bild ist von vorherein ein 2 zu 3 Bereich aufzuspannen
      padding-bottom: 150%;
    }

    .details {
      .name:hover, .name:focus, .name:active {
        text-decoration: none;
      }
    }

    .colortiles {

      .colortiles-container {

        .colortile-click {
          width: remCalc(40);
          height: remCalc(40);

        }

        .colortile {
          width: remCalc(24);
          height: remCalc(24);
          border: 1px solid $gray-line;
        }

        .colortile.selected {

          width: remCalc(18);
          height: remCalc(18);

          //Bei selektieren colortiles müsse die bootstrap classes wieder überschrieben werden mit !important
          &:after {
            content: '';
            position: relative;
            @extend .rounded-circle;
            border: 2px solid $gray-primary;
            display: block;
            height: remCalc(24);
            width: remCalc(24);
            top: remCalc(-4);
            left: remCalc(-4);
          }
        }
      }
    }
  }
}


