// feste Bildgröße
$product-image-width: 80px;
//fester Abstand zwischen Bilder
$product-image-space: 16px;

.order-history-entry{
  .product-image img {
    width: $product-image-width;
  }
  .slick-disabled {
    opacity: 0;
  }
  // Kein BT da erzeugtes HTML von Slick.
  .slick-track {
    margin-left: 0;
  }

  .slick-slide {
    width: $product-image-width+$product-image-space;
  }
}
