.flyout-login {
  z-index: 100;
  right: remCalc(15);
  margin-top: remCalc(1);
  position: absolute;
  display: none;
}

.headerLogin-button {
  cursor: pointer;

  &.active {
    border-bottom: remCalc(1) solid $primary;
    margin: -1px 0;
  }
}

.flyout-close {
  cursor: pointer;
  position: absolute;
  top: $spacer * 0.5;
  right: $spacer * 0.5;
}

.icon-password-show {
  top: remCalc(8);
  right: remCalc(12);
  line-height: remCalc(24);
}

.password-icon-eye {
  @include getIcon("eye", $secondary, 24);
}

.password-icon-eyeSliced {
  @include getIcon("eyeSliced", $secondary, 24);
}

.userFormLogin-mobile {
  .btn-register,
  .btn-login {
    max-width: remCalc(280);
  }
}

.flyout-link {
  a {
    display: block;
    padding: 0.25rem 0;
  }
}

