header {

  //Abstände nur bei searchcomponent innerhalb des headers
  @include media-breakpoint-up(md) {
    .searchbox-wrapper {
      padding-left: 1.5rem;
    }
  }

  @include media-breakpoint-up(lg) {
    .searchbox-wrapper {
      padding-left: 2rem;
      margin-left: 0.5rem;
    }
  }

  .custom-search {
    .search-input {
      transition: width 0.5s;

      @include placeholderColor($gray-primary);

      @include media-breakpoint-only(md) {
        padding-left: remCalc(8);
        position: absolute;
        right: remCalc(0);
        left: auto;

        &:focus {
          padding-left: remCalc(16) !important;
          width: remCalc(250);
        }
      }
    }

    .search-input-absolute {
      padding-right: remCalc(40);
    }
  }
}
