.category-bar {

  min-height: 4.5rem;

  .last-category {
    &:after {
      content: "";
      display: block;
      position: absolute;
      width: 1rem;
      right: -1rem;
      top: 0;
      height: remCalc(1);

      @include media-breakpoint-up(md) {
        width: 2rem;
        right: -2rem;
      }

      @include media-breakpoint-up(lg) {
        display: none;
      }
    }
  }
}