// themespezifische Variablen
// Überschreibt bootstrap Variablen
// Color system

$gray-line:           #E7E7E7;
$gray-secondary:      #828282;
$gray-primary:        #191919;
$gray-surface:        #F2F2F2;
$gray-surface-light:  #F8F8F8;
$gray-light:          #BDBDBD;
$gray-dark:           #D9D9D9;
$white:               #FFF;
$red:                 #eb5050;
$red-velvet:          #FF7575;
$red-coral-light:     #FEC0B9;
$red-coral:           #FE8273;
$red-coral-dark:      #EA5E4D;
$green:               #5AC372;
$green-velvet:        #78E190;
$yellow-sunshine:     #FFCF4E;
$yellow-light:        #E1C989;
$yellow-mud:          #D7A726;
$blue:                #2B94CF;
$dark-light:          #373737;
$black:               $gray-primary !default;

// theme colors
$primary:             $red-coral;
$secondary:           $gray-secondary;
$info:                $blue;
$warning:             $yellow-mud;
$gray:                $gray-line;
$light:               $gray-surface;
$dark:                $gray-primary;

$theme-colors: (
        "gray": $gray,
        "gray-light": $gray-light,
        "gray-dark": $gray-dark,
        "gray-surface": $gray-surface,
        "red-velvet": $red-velvet,
        "red-coral-light": $red-coral-light,
        "red-coral-dark": $red-coral-dark,
        "green": $green,
        "dark-light": $dark-light,
        "green-velvet": $green-velvet,
        "yellow-sunshine": $yellow-sunshine,
        "yellow-mud": $yellow-mud,
        "white": $white,
        "white-alt": $white
);

//Spinner-Color:
$spinner-border-bottom-color:  $primary;

// Body
//
// Settings for the `<body>` element.

$body-color:            $gray-primary;

// Fonts
//
// Font, line-height, and color for body text, headings, and more.

$font-family-base:            "Open Sans", Arial, sans-serif;

$font-size-base:              1rem;
$font-size-lg:                $font-size-base;
$font-size-sm:                $font-size-base;

$font-weight-bold:            600;
$line-height-base:            1;

$h1-font-size: $font-size-base * 4;
$h2-font-size: $font-size-base * 2;
$h3-font-size: $font-size-base * 1.5;
$h4-font-size: $font-size-base * 1.25;
$h5-font-size: $font-size-base;
$h6-font-size: $font-size-base;


$headings-margin-bottom:      1rem;
$headings-font-family:        "Open Sans SemiBold", sans-serif;
$headings-color:              $dark;

$text-16:                     $font-size-base;
$text-14:                     $font-size-base * 0.875;
$text-12:                     $font-size-base * 0.75;
$text-10:                     $font-size-base * 0.625;

// Buttons + Forms
//
// Shared variables that are reassigned to `$input-` and `$btn-` specific variables.

$input-btn-padding-y:         0.4375rem;
$input-btn-padding-x:         0.9375rem;

$input-btn-font-size:         $text-14;
$input-btn-line-height:       1.5;

$input-btn-focus-box-shadow: none;

$input-btn-padding-y-sm:      0.282rem;
$input-btn-padding-x-sm:      $input-btn-padding-x;
$input-btn-font-size-sm:      $input-btn-font-size;

$input-btn-padding-y-lg:      1.4375rem;
$input-btn-padding-x-lg:      $input-btn-padding-x;
$input-btn-font-size-sm:      $input-btn-font-size;

//
// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.
$btn-line-height:             1.7143;

$btn-font-weight: $font-weight-bold;
$btn-box-shadow: none;

$btn-focus-width:             0;

$btn-focus-box-shadow:        none;
$btn-disabled-opacity:        1;
$btn-active-box-shadow:       none;

$btn-link-disabled-color:     $secondary;

//$btn-block-spacing-y:         .5rem !default;

// Allows for customizing button radius independently from global border radius
$btn-border-radius:           .25rem;
$btn-border-radius-lg:        $btn-border-radius;
$btn-border-radius-sm:        $btn-border-radius;

$btn-transition:              color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out !default;

//
// Links
//
$link-color:                  $dark;
$link-decoration:             underline;
$link-hover-color:            $dark;
$link-hover-decoration:       none;


// Boxshadow
// expects $black color-variable above
$box-shadow-sm:               0 .5rem 1.875rem rgba($gray-primary, .06);
$box-shadow-lg:               0 .625rem 1.25rem rgba($gray-primary, 0.15);
$box-shadow-lg-neg:           0 -.625rem 1.25rem rgba($gray-primary, 0.15);

// Forms

$input-color:                   $gray-primary;
$input-border-color:            $gray-line;
$input-focus-border-color:      $gray-primary;
$input-placeholder-color:       $secondary;
$input-disabled-bg:             $gray-surface;

$input-line-height:             1.7143;
$input-focus-width:             0;

$custom-checkbox-indicator-icon-checked: "";
$custom-control-indicator-checked-color: $gray-primary;
$custom-control-indicator-checked-border-color: $gray-primary;
$custom-control-indicator-checked-bg: $white;
$custom-control-indicator-border-color: $gray-primary;

// Form validation

$form-feedback-invalid-color:   $red;
$form-feedback-icon-invalid:    "";


// Badges

$badge-font-size:                   85%;

// Modals

$modal-backdrop-opacity:            .4;

// Popovers

$popover-bg:                        $gray-primary;
$popover-body-color:                $white;
