.text-16 {
  font-size: $text-16;
}

.text-14 {
  font-size: $text-14;
}

.text-12 {
  font-size: $text-12;
}

.text-10 {
  font-size: $text-10;
}

.teaserCategoryLabel {
  letter-spacing: 2px;
}

.secondary-links {
  a {
    color: $secondary;

    &:hover {
      color: $secondary;
    }
  }
}

.link-decoration-inverted {
  a {
    text-decoration: $link-hover-decoration;

    &:hover {
      text-decoration: $link-decoration;
    }
  }
}

.link-decoration-none {
  a {
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }
  }
}

// Normales Verhalten für Ausnahmen im inverted-Bereich
.link-decoration-default {
  a {
    text-decoration: $link-decoration;

    &:hover {
      text-decoration: $link-hover-decoration;
    }
  }
}

.white-links {
  a {
    color: $white;

    &:hover {
      color: $white;
    }
  }
}

.secondary-link {
  color: $secondary;
  text-decoration: none;

  &:hover,
  &.disabled,
  &:disabled,
  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    text-decoration: underline;
    color: $secondary;
  }
}