// themespezifische Styles

// bannercomponents

.bannerComponent {

  $bannerComponentDefaultBgColor: $primary;
  $bannerComponentDefaultColor: $white;

  background-color: $bannerComponentDefaultBgColor;

  .labelWrapper,
  .headlineWrapper,
  .textWrapper,
  .btn-outline-primary {
    color: $bannerComponentDefaultColor;
  }

  .labelWrapper:after {
    background-color: $bannerComponentDefaultColor;
  }

  .btn-outline-primary {
    border: 1px solid $bannerComponentDefaultColor;
  }

  .btn-primary {
    color: $bannerComponentDefaultBgColor;
    background-color: $bannerComponentDefaultColor;
    border: 1px solid $bannerComponentDefaultBgColor;
  }

  .textWrapper-inner {
    background-color: $bannerComponentDefaultBgColor;
    border-bottom-left-radius: remCalc(4);
    border-bottom-right-radius: remCalc(4);
    @include media-breakpoint-up(lg) {
      border-bottom-left-radius: remCalc(0);
      border-bottom-right-radius: remCalc(0);
    }
  }

  .textWrapper-outer {
    background-color: $white;
    @include media-breakpoint-up(lg) {
      background-color: $bannerComponentDefaultBgColor;
    }
  }

  .headlineWrapper {
    @include media-breakpoint-up(md) {
      text-indent: remCalc(-40);
      padding-left: remCalc(40);
      margin-left: remCalc(-40);
    }
  }

  img {
    height: auto;
    width: 100%;
  }
}

.herobanner {
  .textWrapper-inner {
    @include media-breakpoint-up(lg) {
      max-width: remCalc(567);
    }

    .btn {
      display: block !important;
    }
  }

  picture {
    @include media-breakpoint-up(lg) {
      text-align: right;
      height: remCalc(383);
      width: remCalc(600);
    }
    @include media-breakpoint-up(xl) {
      height: remCalc(457);
      width: remCalc(712);
    }

    img {
      @include media-breakpoint-up(lg) {
        height: 100%;
        width: auto;
      }
    }
  }
}

.bigteaserbanner {
  .textWrapper-inner {
    @include media-breakpoint-up(lg) {
      max-width: remCalc(567);
      padding-right: remCalc(40) !important;
      padding-left: remCalc(88) !important;
    }
  }

  picture {
    @include media-breakpoint-up(lg) {
      height: remCalc(383);
      width: remCalc(600);
    }
    @include media-breakpoint-up(xl) {
      height: remCalc(457);
      width: remCalc(712);
    }

    img {
      @include media-breakpoint-up(lg) {
        height: 100%;
        width: auto;
      }
    }
  }
}

.topbanner {
  .textWrapper-inner {
    @include media-breakpoint-up(lg) {
      max-width: remCalc(567);
    }
  }

  picture {
    @include media-breakpoint-up(lg) {
      height: remCalc(383);
      width: remCalc(600);
    }
    @include media-breakpoint-up(xl) {
      height: remCalc(457);
      width: remCalc(712);
    }

    img {
      @include media-breakpoint-up(lg) {
        height: 100%;
        width: auto;
      }
    }
  }
}

.teaserWrapperComponent {
  @include media-breakpoint-up(md) {
    overflow-x: auto;
  }
  overflow-x: auto;

  .teaserbanner {
    .textWrapper-outer {
      @include media-breakpoint-up(lg) {
        background-color: $white !important;
      }
    }

    .headlineWrapper {
      text-indent: 0;
    }

    .textWrapper-inner {
      @extend .mx-3;
      @extend .p-4;
      @include media-breakpoint-up(lg) {
        border-bottom-left-radius: remCalc(4);
        border-bottom-right-radius: remCalc(4);
      }
    }
  }
}

.promobanner {

  @mixin bannerComponentColorMode($name, $color) {
    &.bannerComponent {
      &.textColorMode-#{$name} {

        .labelWrapper,
        .headlineWrapper,
        .textWrapper,
        .btn-outline-primary {
          color: $color;
        }

        .labelWrapper:after {
          background-color: $color;
        }

        .btn-primary {
          background-color: $color;
          border: 1px solid $color;
        }

        .btn-primary:hover {
          background-color: transparent !important;
          border: 1px solid $color !important;
          color: $color !important;
        }

        .btn-outline-primary {
          border: 1px solid $color;
        }

        .btn-outline-primary:hover {
          background-color: $color;
        }
      }
    }

    .teaserTextColorMode-#{$name} {
      color: $color;
    }
  }


  @include bannerComponentColorMode('dark', $dark);
  @include bannerComponentColorMode('light', $white);


  @include media-breakpoint-down(sm) {
    background-color: transparent !important;
    .textWrapper-outer {
      background-color: transparent !important;
    }
  }

  @include media-breakpoint-up(md) {
    .teaserWrapper .altTextWrapper {
      min-height: remCalc(309);
      width: remCalc(240);
    }

    .textWrapper-inner {
      [role="button"] {
        .btn {
          display: block !important;
        }
      }

      [role="button"]:only-child {
        .btn {
          display: inline-block !important;
        }
      }
    }

    .textWrapper {
      -webkit-line-clamp: 2;
    }
  }

  picture {
    width: remCalc(240);
    height: remCalc(309);

    img {
      width: remCalc(240);
      height: remCalc(309);
    }
  }
}
