.herobanner-textbox-component {

  .image-container {
    max-width: remCalc(1600);
  }

  .teaser-box {
    background-color: transparentize($gray-line, 0.2);
    @include media-breakpoint-up(lg) {
      max-height: remCalc(275);
      max-width: remCalc(425);
      position: absolute;
      top: 50%;
      right: 10%;
      &-left {
        left: 10%;
      }
      transform: translateY(-50%)
    }
  }

  .teaser-text {
    color: $secondary;
  }

  .headline {
    line-height: 2.5rem;
  }
}