.header-icon {
  .icon {
    .icon-overlay {
      position: absolute;
      width: 1rem;
      height: 1rem;
      top: remCalc(-6);
      right: remCalc(-7);

      .icon-logged-in {
        @include getIcon("checkmarkSmall", $white, 16);
      }
    }
  }

  .default-icon-login:hover {
    @extend .default-icon-loginFill;
  }

  .default-icon-bag:hover {
    @extend .default-icon-bagFill;
  }
  .default-icon-quickOrder:hover {
    @extend  .default-icon-quickOrderFill;

  }
  .default-icon-heartLarge:hover {
    @extend .default-icon-heartLargeFill;
  }
  a:hover {
    text-decoration: none;
  }
}

.bg-icon-mobile {
  width: 2.5rem;
  height: 2.5rem;
}
