$button-icon-list: (
        chevronRight,
        chevronLeft,
        chevronDown,
        plus,
        minus,
        pencil,
        close,
        heart,
        filter,
        envelope,
        questionMark,
);

// Codiert die URL und setzt Farbe und Größe des Icon
@function getEncodedIconUrl ($icon-url, $icon-color, $icon-size) {
  $icon-url: str-replace($icon-url, $icon-color-placeholder, $icon-color);
  $icon-url: str-replace($icon-url, $icon-size-placeholder, $icon-size);
  @return url("data:image/svg+xml;charset=utf-8,#{svg-encode($icon-url)}");
}

// Gibt anhand des Namens, der Farbe und Größe eine passende URL zurück
@function getIconUrl ($icon-name, $icon-color, $icon-size) {
  @if map-has-key($icon-url-map, $icon-name) {
    $icon-url: map-get($icon-url-map, $icon-name);
    @return getEncodedIconUrl($icon-url, $icon-color, $icon-size);
  } @else {
    @warn "Unfortunately, no value could be retrieved from `#{$icon-name}`. Please make sure it is defined in `$icon-map`.";
  }
}

// Erstellt eine Map aus _allen_ Icons anhand der Farbe und Größe
@function getIconUrlMap ($icon-color, $icon-size) {
  $result-map: ();
  @each $icon-name, $icon-url in $icon-url-map {
    $result-map: map_merge($result-map, ($icon-name: getEncodedIconUrl($icon-url, $icon-color, $icon-size)));
  }
  @return $result-map;
}

// Erstellt eine Map aus den Icons für Buttons anhand der Farbe und Größe
@function getButtonIconUrlMap ($icon-color, $icon-size) {
  $result-map: ();
  @each $icon-name in $button-icon-list {
    $result-map: map_merge($result-map, ($icon-name: getIconUrl($icon-name, $icon-color, $icon-size)));
  }
  @return $result-map;
}

// Mixin zum erstellen eines Icons anhand von Name, Farbe und Größe
@mixin getIcon($icon-name, $icon-color, $icon-size) {
  background-image: getIconUrl($icon-name, $icon-color, $icon-size);
  background-repeat: no-repeat;
  width: remCalc($icon-size);
  height: remCalc($icon-size);
}

// default Icon Map erstellen:
$default-icon-color: $dark;
$default-icon-size: 24;
$default-icon-url-map: getIconUrlMap($default-icon-color, $default-icon-size);

@each $icon-name, $icon-url in $default-icon-url-map {
  .default-icon-#{$icon-name} {
    @include getIcon($icon-name, $default-icon-color, $default-icon-size)
  }
}

// Mixin zum erstellen eines Icons anhand von Name, Farbe und Größe
@mixin getFontIcon($icon-char, $icon-color, $icon-size, $font-class) {
  width: remCalc($icon-size);
  height: remCalc($icon-size);
  position: relative;
  display: inline-block;
  font-size: 0;
  vertical-align: middle;
  text-align: center;
  @extend .border-0;
  &::before{
    @extend #{$font-class};
    content: $icon-char;
    color: $icon-color;
    width: remCalc($icon-size);
    height: remCalc($icon-size);
    line-height: remCalc($icon-size)!important;
    position: absolute;
    left: 0;
    top: 0;
    display: inline-block;
  }
}

