@include media-breakpoint-up(lg) {
  .container {
    padding-left: 0;
    padding-right: 0;
  }
}

@include media-breakpoint-down(md) {
  .container {
    max-width: none;
  }
}

@include media-breakpoint-only(md) {
  .container {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.popover {
  z-index: 1;
}

.rounded, .rounded-circle {
  background-clip: padding-box;
}

.close {
  float: none;
  opacity: 1;
}

.alert-dismissible {
  .close {
    padding: 0.5rem;
  }
}