.language-selector {
  //Die Buttons aussehen lassen wie normalen Text
  .language {
    outline: none;
    background: none;
    cursor: pointer;
    font-family: inherit;
    font-size: inherit;
  }
  .language.selected {
    cursor: auto;
  }

  button:hover {
    text-decoration: underline;
  }
}

footer .language-selector {
  //Hier kann man für die Farbe keine Bootstrap Klasse an der Komponente nutzen, da die Komponenten an verschiedenen
  //Stellen unterschiedliche Farben hat.
  color: $white;
  .language {
    color: $white;
  }
}

header .language-selector {
  .languageprefix {
    display: none;
  }
  .language {
    color: $dark;
  }
}

#sideMenu .language-selector .languageprefix {
  display: inline;
}

