$sideBarWidthFFFilter: remCalc(500);
@include media-breakpoint-up(md) {

  .canvas.moved-from-sidebar.right {
    left: -$sideBarWidthFFFilter;
  }

  .filterComponent.sidebar {
    width: $sideBarWidthFFFilter;
    &.sidebar-right {
      right: -$sideBarWidthFFFilter;
      &.active {
        right: 0;
      }
    }
  }
}

.filterComponent {
  .filterMenuBody {
    .filterMenuUpper {
      min-height: 0;

      .filterMenuSlot {
        // Ermöglicht es flex Containern kleiner als Parent zu werden
        min-height: 0;
      }
    }
  }
}