ff-asn {
  ff-asn-group, ff-asn-group-slider {

    max-width: none;

    .clusterLevel1 {
      @extend .ml-3;
    }

    .btn-collapsible {
      &::before {
        background-image: getIconUrl("plus", $dark, 24);
      }

      // Alle Button 1 Pixel nach oben schieben, damit sich die Borders perfekt überlagern ohne dicker zu werden
      margin-top: -1px;
    }

    &[opened] {
      .btn-collapsible::before {
        background-image: getIconUrl("minus", $dark, 24);
      }
    }

    .filtervalue {
      cursor: pointer;
    }

    .filtercolortilewrapper {
      height: remCalc(48);
      width: remCalc(48);

      &:before {
        @include getIcon("checkmark", $black, 32);
        content: '';
        float: left;
        position: relative;
        top: remCalc(8);
        left: remCalc(7);
        z-index: 1;
      }

      &.dark:before {
        @include getIcon("checkmark", $white, 32);
      }

      &:after {
        content: '';
        position: relative;
        @extend .rounded-circle;
        border: 1px solid $gray-primary;
        display: block;
        height: remCalc(48);
        width: remCalc(48);
        top: remCalc(-38);
      }
    }

    .filtercolortile {
      height: remCalc(48);
      width: remCalc(48);

      &.selected {
        height: remCalc(38);
        width: remCalc(38);
        top: remCalc(5);
      }
    }

    .resetFilter {
      cursor: pointer;
    }

    .underline-on-hover {
      &:hover {
        text-decoration: $link-decoration;
      }
    }
  }

  ff-asn-group-slider {

    .ffw-wrapper {
      //Das kann ich nicht in der jsp machen, das sind implizit von factfinder gerenderte Elemente wo ich nichts modifizieren kann
      @extend .d-flex;
      @extend .flex-column;

      .removeFilter {
        cursor: pointer;
        width: 84%;
      }

      .ffw-sliderSection {
        @extend .mt-0;

        input {
          width: remCalc(100);
        }
      }
    }

    ff-slider {
      width: 84%;
      height: remCalc(4);

      .sliderBtn {
        height: remCalc(24);
        width: remCalc(24);
        position: relative;
        bottom: remCalc(10);
      }
    }
  }
}

.deleteAllFilters {
  cursor: pointer;
}

.hiddenScrollbar {
  overflow-y: scroll;

  &::-webkit-scrollbar {
    width: 0;
  }

}