.ff-suggest-wrapper {

  @include media-breakpoint-up(lg) {
    margin-left: remCalc(12) !important;
  }

  //Fact-Finder Klasse (kein Zugriff auf DOM-Element in JSP)
  .ffw-suggestContainerWrapper {
    width: 100%;

    @include media-breakpoint-only(md) {
      &.suggest-focus {
        width: remCalc(275);
        position: absolute;
        right: remCalc(0);
        left: auto;
      }
    }
  }

  .suggest-line {
    height: 0;
    border: remCalc(1) solid $gray-line;
  }

  ff-suggest-item {
    //FF-Suggest-Item Hover Styling überschreiben
    &:hover {
      text-decoration: none;
    }

    //Fact-Finder Klasse für Pfeiltastatursteuerung durch Suggests
    &.ffw-highlight-suggest-item {
      background-color: $light;
    }

    //Fact-Finder Klasse für Hervorhebung der Übereinstimmung von Suche und Suggests
    .ffw-query {
      @extend .display-22;
    }
  }
}