.seotext {
  column-count: 2;
}

.readMoreButton {
  bottom: 0;
  right: 0;
  left: 0;
  height: 10rem;
  background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 45%);
}

.seo-arrow-down {
  @include getIcon("chevronDown", $secondary, 24);
}

@include media-breakpoint-down(sm) {
  .seotext {
    column-count: auto;
  }
  .seotext-mobile-collapsed {
    overflow: hidden;
    max-height: 460px;
  }
}
