$sideBarWidthSizeSelector: remCalc(500);
@include media-breakpoint-up(md) {

  .canvas.moved-from-sidebar.right {
    left: -$sideBarWidthSizeSelector;
  }

  .size-selector.sidebar {
    width: $sideBarWidthSizeSelector;
    &.sidebar-right {
      right: -$sideBarWidthSizeSelector;
      &.active {
        right: 0;
      }
    }
  }
}
