.list-custom-checkmark {
  list-style-type: none;
  @extend .display-23;
  > * {
    @extend .position-relative;
    &::before {
      display: inline-block;
      position: absolute;
      @extend .icon-svg;
      @extend .icon-checkmark-green-20;
      left: $spacer * -1.5;
      top: $spacer / 16;
      content: '';
    }
  }
  &.display-21 {
    > * {
      @extend .display-21;
      &::before {
        top: $spacer / 5.333;
      }
    }
  }
}

.list {
  &--dashed {
    list-style: none;

    > li:before {
      display: inline-block;
      content: "-";
    }
  }
}
