// feste Bildgröße
$gallery-image-width-lg: 60px;
$gallery-image-width-xl: 73px;
$main-image-width-lg: 410px;
$main-image-width-xl: 468px;

.product-gallery {

  .product-gallery-img {
    cursor: pointer;
  }
  @include media-breakpoint-only(lg) {
    width: $gallery-image-width-lg;
  }
  @include media-breakpoint-only(xl) {
    width: $gallery-image-width-xl;
  }
}
.product-main-img {
  @include media-breakpoint-only(lg) {
    width: $main-image-width-lg;
  }
  @include media-breakpoint-only(xl) {
    width: $main-image-width-xl;
  }
}
.product-carousel-prev {
  left: 0;
  top: calc(50% - 20px);
  opacity: 0.7;
}
.product-carousel-next {
  right: 0;
  top: calc(50% - 20px);
  opacity: 0.7;
}