.category-slider {
  .category-button {
    bottom: 10%;
  }

  .category-slide {
    img {
      max-width: 100%;
      height: auto;
    }
  }

  .slick-slide {
    margin: 0 remCalc(10);

    &:not(.slick-current) {
      img {
        filter: grayscale(100%);
      }
    }
  }

  .slick-list {
    margin: 0 remCalc(-10);
  }

  .category-slider-next {
    right: remCalc(-35);
    top: calc(50% - 54px);

    @include media-breakpoint-only(lg) {
      right: remCalc(-25);
    }
  }

  .category-slider-prev {
    left: remCalc(-35);
    top: calc(50% - 54px);

    @include media-breakpoint-only(lg) {
      left: remCalc(-25);
    }
  }

  .slick-arrow {
    &.slick-disabled {
      visibility: hidden;
    }
  }
}
