// feste Bildgröße
$product-image-width-mobile: 94px;
$product-image-width-desktop: 128px;

// Position Anzahl-Icon
$quantity-icon-top: 0.75rem;
$quantity-icon-left: 0.75rem;

.cart-items, .order-items, .add-to-cart-modal, .quick-order-item {
  .product-image {
    width: $product-image-width-mobile;
    img {
      width: 100%;
    }
  }
  .next-to-product-image {
    //Abstand 1rem
    width: calc(100% - #{$product-image-width-mobile} - 1rem);
  }

  @include media-breakpoint-up(md) {
    .product-image {
      width: $product-image-width-desktop;
    }
    .next-to-product-image {
      width: calc(100% - #{$product-image-width-desktop} - 1rem);
    }
  }
}

.multiple-add-to-cart-modal {
  .product-image {
    width: $product-image-width-desktop;
    img {
      width: 100%;
    }

    .product-fade {
      opacity: .5;
      top: -$quantity-icon-top;
      left: -$quantity-icon-left;
      height: calc(100% + #{$quantity-icon-top});
      width: calc(100% + #{$quantity-icon-left});
    }
  }
}

.cart-items, .order-items, .add-to-cart-modal, .quick-order-item, .multiple-add-to-cart-modal {
  .sold {
    .product-image, .product-name, .product-variants, .product-price {
      opacity: .5;
    }
  }

  .product-amount {
    top: -$quantity-icon-top;
    left: -$quantity-icon-left;

    .product-amount-number {
      top: 0;
      left: 0;
    }
  }
}
