// buttons

.btn {
  &.disabled,
  &:disabled {
    cursor: default;
  }
}

// inline buttons with min-width 160px
// overwrite this with .btn-block
.btn:not(.btn-block) {
  min-width: 10rem;
  max-width: 100%;
  @include media-breakpoint-up(md) {
    display: inline-block;
  }
}

// for btn height see Bootstrap-class --> .btn-sm + .btn-lg
// this class is only used for .btn-sm with small text
.btn-small {
  @extend .display-29;
  padding: 0.1875rem $input-btn-padding-x-sm;
}

.btn-large {
  @extend .display-9;
}

$icon-width: 24;

.btn.icon-svg {
  &::before {
    content: '';
    position: relative;
    display: inline-block;
    left: 0;
    top: 0;
    width: #{$icon-width}px;
    height: #{$icon-width}px;
    vertical-align: bottom;
  }

  &.icon-left {
    text-indent: -#{$icon-width}px;

    &::before {
      float: left;
    }
  }

  &.icon-right {
    text-indent: #{$icon-width}px;

    &::before {
      float: right;
    }

    &.no-indent {
      text-indent: 0;
    }
  }

  &.icon-inline {
    &.icon-left,
    &.icon-right {
      text-indent: 0;

      &::before {
        float: none; // todo: cx-211 inline right --> ::after
        margin-right: remCalc(8);
      }
    }
  }
}

.btn:not(.btn-link) {
  text-decoration: none;
}

.btn-link {
  text-decoration: $link-decoration !important;

  &:hover {
    text-decoration: $link-hover-decoration !important;
  }
}

// textlinks (als Button, nicht inline-Text)
.btn-link.icon-svg {
  $fill-color: $dark;
  $icon-map: getButtonIconUrlMap($fill-color, $icon-width);
  @each $key, $bgImageUrl in $icon-map {
    &.icon-#{$key}::before {
      background-image: $bgImageUrl;
    }
  }

  &.icon-red-heartFill::before {
    background-image: getIconUrl("heartFill", $danger, $icon-width);
  }

  &.disabled,
  &:disabled {
    text-decoration: none;
    color: $gray-light;
    $icon-map: getButtonIconUrlMap($fill-color, $icon-width);
    @each $key, $bgImageUrl in $icon-map {
      &.icon-#{$key}::before {
        background-image: $bgImageUrl;
      }
    }
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    text-decoration: none;
  }
}

