.btn-collapsible {
  &.btn.icon-svg.icon-right {
    text-indent: 0;
  }

  &:focus {
    box-shadow: none;
  }

  &::before {
    background-image: getIconUrl("minus", $dark, 24);
  }

  &.collapsed::before {
    background-image: getIconUrl("plus", $dark, 24);
  }

  &.btn-light {
    &:hover {
      background-color: $gray-surface;
    }

    &:not(:disabled):not(.disabled):active,
