.close-modal {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
}

.pw-reset-circle {
  margin-top: remCalc(-40);
  height: remCalc(121);
  width: remCalc(121);
}

.pw-reset-close {
  @include getIcon("close", $white, 24);
}

.pw-reset-success {
  @include getIcon("checkmarkSmall", $success, 90);
}

.pw-reset-error {
  @include getIcon("close", $danger, 90);
}

.pw-reset-warning {
  @include getFontIcon('!', $warning, 90, ".display-1");
}

.pw-reset-warning-close {
  @include getIcon("close", $warning, 24);
}

.pw-reset-next-arrow {
  @include getIcon("chevronRight", $secondary, 56);
}

.pw-reset-steps {
  .icon-show-step {
    position: absolute;
    top: 0;
    left: 0;
    height: remCalc(20);
    width: remCalc(20);
  }
}

@include media-breakpoint-down(xs) {
  .modal-body-graphic {
    svg {
      height: remCalc(85);
      width: remCalc(85);
    }
  }
}

@include media-breakpoint-up(sm) {
  .pw-reset-steps {
    .icon-show-step {
      height: remCalc(32);
      width: remCalc(32);
    }
  }
}
