.first-level-nav, .second-level-nav{
  a:hover {
    text-decoration: none;
  }
}

.first-level-nav {
  .nav-elem{
    &:hover {
      @extend .border-dark;
    }
  }
}

.second-level-nav {
  .nav-elem{
    &.active, &:hover {
      @extend .border-primary;
    }
  }

  nav {
    display: none;

    &.active {
      display: block;
    }
  }

  .navigation-flyout-opener {
    cursor: pointer;
  }
}

.second-level-nav-mobile {
  overflow-y: auto;

  .collapse-sidemenu.active {
    background-color: $light;
  }
}

.navigation-flyout {
  height: remCalc(548);

  .navigation-flyout-closer {
    top: 0;
    right: 0;
  }

  .navigation-list {
    margin-right: -$grid-gutter-width/2;
    margin-left: -$grid-gutter-width/2;

    .navigation-headline {
      height: 0;
      position: relative;
      top: -3rem;
    }

    .navigation-element {
      padding-right: $grid-gutter-width/2;
      padding-left: $grid-gutter-width/2;
    }

    .navigation-linebreak {
      width: 0;
      height: 101%;
    }
  }
}
